section {
  position: relative;
  h2 {
    font-size: 30px;
    font-weight: 400;
    color: #484848;
    margin-bottom: 60px;
    text-transform: capitalize;
  }
  .flights {
    margin-top: 100px;
    .flights_title {
      display: flex;
      justify-content: space-between;
      .right_title {
        display: flex;
        p {
          font-weight: 500;
          font-size: 16px;
          color: #484848;
          text-transform: capitalize;
          margin-right: 35px;
          transition: 500ms;
          &:hover {
            color: #ff4b64;
            text-shadow: 2px 2px 2px white;
            &::after {
              content: "";
              display: block;
              height: 2px;
              width: 50%;
              background-color: #ff4b64;
              margin: auto;
              margin-top: 5px;
              box-shadow: 2px 2px 1px black;
            }
          }
        }
      }
    }
    .airline_title {
      p {
        text-transform: capitalize;
        color: #484848;
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 40px;
      }
    }
    p {
      font-weight: 400;
      font-size: 16px;
      color: #484848;
      text-transform: capitalize;

      span {
        display: block;
        margin-top: 20px;
        color: #9a9a9a;
        text-transform: uppercase;
      }
    }
    .flights_airline {
      display: flex;
    }
    .box_imges {
      margin-right: 20px;
      align-self: center;
    }
    .transavia,
    .delta,
    .vueling,
    .air_france,
    .american {
      padding: 50px 0px;
      border-top: 1px solid #9a9a9a;
      border-bottom: 1px solid #9a9a9a;
    }
    .flights_price {
      p {
        color: #ff4b64;
        padding: 17px 36px;
        border: 2px solid #ff4b64;
        border-radius: 10px;
        text-align: center;
        font-size: 16px;
        font-weight: 500;
      }
    }
  }

  .hotels {
    margin-top: 80px;
    .hotels_title {
      display: flex;
      justify-content: space-between;
      .right_title {
        display: flex;
        p {
          font-weight: 500;
          font-size: 16px;
          color: #484848;
          text-transform: capitalize;
          margin-right: 35px;
          transition: 500ms;
          &:hover {
            color: #ff4b64;
            text-shadow: 2px 2px 2px white;
            &::after {
              content: "";
              display: block;
              height: 2px;
              width: 50%;
              background-color: #ff4b64;
              margin: auto;
              margin-top: 5px;
              box-shadow: 2px 2px 1px black;
            }
          }
        }
      }
    }
    .hotel_text{
        border-left: 1px solid #9a9a9a;
        border-right: 1px solid #9a9a9a;
        border-bottom: 1px solid #9a9a9a;
        padding: 27px 15px 25px ;
        h3{
            font-weight: 500;
            font-size: 16px;
            color: #484848;
            text-transform: capitalize;
            margin-bottom: 30px;
        }
        p{
            display: flex;
            justify-content: space-between;
            color:#9a9a9a;
            font-size: 16px;
            font-weight: 400;
            text-transform: capitalize;
        }
        .price{
            color: #ff4b64;
            padding: 10px 30px;
            border: 2px solid #ff4b64;
            border-radius: 10px;
            width: 30%;
            margin-top: 25px;
        }
    }
  }

  .attractions{
      margin-top: 80px;
    .attractions_title {
        display: flex;
        justify-content: space-between;
        .right_title {
          display: flex;
          p {
            font-weight: 500;
            font-size: 16px;
            color: #484848;
            text-transform: capitalize;
            margin-right: 35px;
            transition: 500ms;
            &:hover {
              color: #ff4b64;
              text-shadow: 2px 2px 2px white;
              &::after {
                content: "";
                display: block;
                height: 2px;
                width: 50%;
                background-color: #ff4b64;
                margin: auto;
                margin-top: 5px;
                box-shadow: 2px 2px 1px black;
              }
            }
          }
        }
      }
  }
    .trips{
        margin-top: 80px;
        .box_img{
            height: 100%;
            img{
                width: 100%;
                height: 100%;
            }
        }
        .trips_title {
            display: flex;
            justify-content: space-between;
            .right_title {
              display: flex;
              p {
                font-weight: 500;
                font-size: 16px;
                color: #484848;
                text-transform: capitalize;
                margin-right: 35px;
                transition: 500ms;
                &:hover {
                  color: #ff4b64;
                  text-shadow: 2px 2px 2px white;
                  &::after {
                    content: "";
                    display: block;
                    height: 2px;
                    width: 50%;
                    background-color: #ff4b64;
                    margin: auto;
                    margin-top: 5px;
                    box-shadow: 2px 2px 1px black;
                  }
                }
              }
            }
          }
        .trips_texts{
            border: 1px solid #9a9a9a;
            border-left: none;
            padding: 23px 40px 40px 30px;
            h3{
                font-weight: 500;
                font-size: 16px;
                color: #484848;
                text-transform: capitalize;
                margin-bottom: 30px;
            }
            p{
                font-weight: 400;
                font-size: 16px;
                color: #484848;
                line-height: 30px;
            }
        }
    }
  .mb_20{
      margin-bottom: 20px;
  }
}
