@import "./header.less";
@import "./section.less";
@import "./params/var.less";
@import "./params/mix.less";
@import "./footer.less";

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: Lato;
  .click_top {
    position: fixed;
    right: 90px;
    display: none;
    top: 300px;
    z-index: 111;
    .sm-block({
        right: 10px;
        top: 100px;
    });
    .scroll_top {
      color: #ff4848;
      background-color: transparent;
      font-size: 40px;
      border-radius: 5px;
      border: none;
      text-shadow: 2px 2px 2px black;
      .sm(font-size, 30px);
    }
  }
  .btn{
    border-radius: 10px;
    background-color: transparent;
    border: 2px solid white;
    color: white;
    padding: 15px 35px;
    font-size: 16px;
    text-align: center;
    transition: 500ms;
    text-transform: capitalize;
  }
  .box_img{
    width: 100%;
    height: auto;
  }
}
