header {
  background: url(../img/headerbg.png) no-repeat center center / cover;
  width: 100%;
  height: auto;
  position: relative;
  .header_top {
    padding-top: 40px;
    .navbar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      ul {
        display: flex;
        li {
          margin: 0px 15px;
          .sm-block({
            margin: 0px 5px;
          });
          .nav_links {
            .md(display, none );
            .sm(font-size, 14px);
            text-decoration: none;
            color: white;
            text-transform: capitalize;
            font-size: 16px;
            transition: 500ms;
            &:hover {
              color: orangered;
              text-shadow: 2px 2px 10px black;
            }
          }
          .logo {
            font-size: 24px;
            margin-right: 60px;
            margin-left: 0;
            text-decoration: none;
            color: white;
            text-transform: capitalize;
            &:hover {
              color: orangered;
              text-shadow: 2px 2px 10px black;
            }
          }
        }
      }
      .login {   
        border-radius: 10px;
        background-color: transparent;
        border: 2px solid white;
        color: white;
        padding: 15px 35px;
        font-size: 16px;
        transition: 500ms;
        &:hover {
          box-shadow: 2px 2px 10px black;
          border: 2px solid #ff4848;
        }
        i{
          display: none;
          .md(display, block);
        }
        span{
           display: block;
          .md(display, none);
        }
        .md-block({
          display: none;
          padding: 10px;
          border-radius: 50%;
        
        });
        .sm-block({
          display: none;
          padding: 5px;
          border-radius: 50%;
          font-size: 14px;
        });
      }
    }
  }
  .bars {
    .bar {
      padding: 12px;
      transition: 500ms;
      &:hover {
        box-shadow: 2px 2px 10px black;
        border: 2px solid #ff4848;
      }
      .sm-block({
        display: block;
        padding: 5px;
        border-radius: 50%;
      });
    }
  }
  .header_title {
    margin-top: 20%;
    width: 100%;
    height: auto;
    h1 {
      color: white;
      font-size: 72px;
      font-weight: 500;
      text-align: center;
      margin-bottom: 50px;
      .md-block({
        font-size: 50px;
        margin-bottom: 30px;
      });
      .sm-block({
        font-size: 30px;
        margin-bottom: 20px;
      });
    }
    p {
      font-weight: 400;
      font-size: 30px;
      text-transform: capitalize;
      text-align: center;
      color: white;
      padding-bottom: 20%;
      .md-block({
        font-size: 30px;
        margin-bottom: 30px;
      });
      .sm-block({
        font-size: 20px;
        margin-bottom: 20px;
      });
    }
  }
  .form_search {
    position: absolute;
    bottom: -45px;
    width: 100%;
    form {
      display: flex;
      width: 100%;
      input {
        border: none;
        border-right: 1px solid #e6e6e6;
        border-bottom: 1px solid #e6e6e6;
        padding: 20px 70px 20px 30px;
        color: #818181;
        font-size: 16px;
        text-transform: capitalize;
      }
      .search {
        background-color: #ff4b64;
        color: white;
        padding: 35px 20px;
        border: none;
        font-size: 16px;
        border-radius: unset;
      }
    }
  }
}
